a {
  text-decoration: none;
}

a:hover {
  color: var(--accent);
}

.center {
  display: flex;
  flex-direction: column;
  place-items: center;
}

.color {
  color: var(--accent);
}

.App {
  height: 100%;
  width: 100%;
}

.header {
  height: calc(50% - 28px);
  padding: 40px 15px;
}

nav {
  height: 56px;
  min-height: 56px;
  width: 100%;
  background-color: #2C2C2C;
  box-shadow: 0px 1px 3px 1px rgba(0,0,0,0.3);
  display: flex;
  place-items: center;
  justify-content: center;
}

nav > a {
  color: white;
  margin-right: 30px;
}

.main {
  height: 100%;
  width: 100%;
  justify-content: center;
}

.main h1 {
  text-align: center;
}

.search-bar {
  flex-direction: row;
  width: 100%;
  border-radius: 10px;  
  max-width: 912px;
  /* height: 91px; */
  height: 75px;
  box-shadow: 0 1rem 2rem rgba(0, 0, 0, 0.25);
  background-color: white;
}

.search-bar img {
  height: 26px;
  width: 26px;
  margin: 20px;
}

.search {
  border: none;
  border-radius: 10px;
  width: 100%;
  font-size: 25px;
}

.search-bar:focus-within {
  border: 1px solid var(--accent);
}

.search:focus-within {
  outline: none;
}

.card {
  justify-content: center;
  background-color: black;
  color: white;
  margin: 10px;
  border-radius: 15px;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
  box-shadow: rgba(53, 45, 71, 0.75) 0px 0px 0px 1000px inset;
  cursor: pointer;
  /* box-shadow: 0px 7px 10px rgba(0, 0, 0, 0.5); */
  width: 280px;
  height: 360px;
}

.card:nth-child(1) {
  background-image: url(https://upload.wikimedia.org/wikipedia/en/4/45/Wasteland_3_cover_art.jpg);
}

.card:nth-child(2) {
  background-image: url(https://www.team17.com/wp-content/uploads/2016/06/overcooked-featured-1260x709.jpg);
}

.card:nth-child(3) {
  background-image: url(https://media.playstation.com/is/image/SCEA/minecraft-starter-collection-squareboxart-01-ps4-11dec19-en-us?$native_nt$);
}

.card:nth-child(4) {
  background-image: url(https://upload.wikimedia.org/wikipedia/en/5/5e/Fall_Guys_cover.jpg);
}

.card button {
  padding: 8px 32px;
  background-color: transparent;
  font-size: 14px;
  border-radius: 4px;
  border: 1px solid #fff;
  color: white;
  background: hsla(0,0%,100%,.16);
  cursor: pointer;
}

.card:hover > * {
  color: var(--accent);
  border-color: var(--accent);
}

.card:hover > button {
  background-color: hsla(0,0%,100%,.25);
}

.browse {
  height: calc(100% - 56px);
  max-width: 1520px;
  /* max-width: 1380px; */
  margin: 0 auto;
  min-width: 320px;
  /* padding-left: 20px;
  padding-right: 20px; */
  padding-left: 100px;
  padding-right: 100px;
}

.browse > * {
  margin-top: 30px;
}

.filter-input {
  padding: 11px 16px;
  /* width: 240px; */
  width: 180px;
  height: 38px;
  box-shadow: 0 4px 16px 0 rgba(22, 42, 90, 0.12);
  transition: box-shadow .3s ease;
  border-radius: 8px;
  background: #fff;
  font-size: 14px;
  /* min-height: 44px; */
  border: none;
  outline: none;
  margin-right: 45px;
}

.filter-input::placeholder {
  color: black;
}

.name {
  padding: 10px 0;
}

.filters {
  display: flex;
}

.game-list {
  margin-top: 50px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.game-card {
  flex: 1 0 16%;
  margin-bottom: 50px;
  color: black;
}

.banner {
  background-image: url(https://wallpaperaccess.com/full/3511118.jpg);
  background-position: 50% 35%;
  /* background-position: center center; */
  background-repeat: no-repeat;
  background-size: cover;
  /* height: 400px; */
  /* height: 442px; */
  /* height: 600px; */
  height: 460px;
  width: 100%;
}

.container {
  /* display: flex;
  flex-direction: column;
  place-items: center; */
}

.carousel {
}

.carousel-items {
  display: flex;
  margin-top: 25px;
  /* justify-content: space-around; */
  /* flex: 1 0 16%; */
}

.carousel-item {
  margin-right: 15px;
}

.section2 {
  display: flex;
  flex-wrap: wrap;
  max-width: 1200px;
  width: 100%;
  height: 100%;
  /* margin-left: auto;
  margin-right: auto; */
}

.game-categories {
  min-height: 360px;
  /* height: calc(50% - 28px); */
  width: 100%;
  background-color: white;
  justify-content: center;
}

.card2 {
  width: 185px;
  height: 265px;  
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
  background-image: url(https://upload.wikimedia.org/wikipedia/en/5/5e/Fall_Guys_cover.jpg);
  /* margin-right: 45px; */
  /* margin-right: 6px; */
}

@media only screen and (max-width: 1200px)  {
  .section2 {
    max-width: 600px;
  }
}

@media only screen and (max-width: 600px)  {
  .section2 {
    max-width: 300px;
  }
}